import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {BehaviorSubject, Observable} from 'rxjs';
import {ActivatedRouteSnapshot, Resolve, RouterStateSnapshot} from '@angular/router';
import { AngularFireDatabase } from '@angular/fire/database';
import { promise } from 'protractor';
@Injectable({
  providedIn: 'root'
})
export class CctvCam1DashboardService implements Resolve<any> {
  widgetsUrl = 'api/dashboard-crm-widgets';
  private tasksUrl = 'api/tasks-list';  // URL to web api
 
  charts={};
  cameraList={};
  widgets: any;
  tasks: any[];
  // barchart: any;
  // piechart: any[];
  onTasksChanged: BehaviorSubject<any> = new BehaviorSubject([]);

  constructor(private http: HttpClient, private db:AngularFireDatabase) {
  }

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<any> | Promise<any> | any {
    return new Promise((resolve, reject) => {

      Promise.all([
        this.getWidgets(),
        this.getTasksList(),
        this.getChartDetails(),
        this.getCamDetails()
      ]).then(
        () => {
          resolve();
        },
        reject
      );
    });
  }



  getWidgets(): Promise<any> {
    return new Promise((resolve, reject) => {
      this.http.get(this.widgetsUrl)
        .subscribe((response: any) => {
          this.widgets = response;
          resolve(response);
        }, reject);
    });
  }

  /**
   * Get all tasks
   * @returns {Promise<any>}
   */
  getCamDetails():Promise<any[]>{
    let self = this;
   return new Promise((resolve,reject)=>{
     let dbRef=this.db.database.ref('CamMasterDetails');
     var x= this;
     dbRef.on('value', ((snapshot) => {
      console.log(x.charts);
       console.log(snapshot.val());
       x.cameraList= snapshot.val();
       resolve(x.widgets);
    }),reject);

   });
  }

  getChartDetails():Promise<any>{
    let self = this;
   return new Promise((resolve,reject)=>{
     let dbRef=this.db.database.ref('VehicleMasterData');
     var x= this;
     dbRef.on('value', ((snapshot) => {
      console.log(x.charts);
       console.log(snapshot.val());
       x.charts = snapshot.val();
       resolve(x.charts);
    }),reject);

   });
  }
  getTasksList(): Promise<any[]> {
    return new Promise((resolve, reject) => {
      this.http.get(this.tasksUrl)
        .subscribe((response: any) => {
          this.tasks = response;
          this.onTasksChanged.next(this.tasks);
          resolve(this.tasks);
        }, reject);
    });
  }

  updateTask(task: any) {
    return new Promise((resolve, reject) => {
      this.http.post(this.tasksUrl + '/' + task.id, {...task})
        .subscribe(response => {
          this.getTasksList().then(tasks => {
            resolve(tasks);

          }, reject);
        });
    });
  }
}
