import { ChartType, ChartOptions ,ChartDataSets} from 'chart.js';
import { SingleDataSet, Label, monkeyPatchChartJsLegend, monkeyPatchChartJsTooltip } from 'ng2-charts';
import * as pluginAnnotations from 'chartjs-plugin-annotation';
import * as pluginDataLabels from 'chartjs-plugin-datalabels';

export class  PieChartModel { 
    public pieChartOptions: ChartOptions = {
        responsive: true,
        // plugins: {
        //   datalabels: {
        //     anchor: 'end',
        //     align: 'end',
        //   }
        // },
        title: {
          display: true,
          text: 'VECHICLE TYPE',
          padding:10,
          position:'bottom'
      }, 
      animation: 
            {
            duration: 500 // general animation time
            },
            hover: {
            animationDuration: 0 // duration of animations when hovering an item
            },
            responsiveAnimationDuration: 100 ,
           
      //   animation: {
      //     onProgress: function(animation) {
      //       debugger;
      //         progress.value = animation.animationObject.currentStep / animation.animationObject.numSteps;
      //     }
      // },
        legend: {
          position: 'bottom',
          labels:{
            fontSize:10,
            fontColor:'white',
            usePointStyle:true
          }
        },
        plugins: {
          datalabels: {
            formatter: (value, ctx) => {
              const label = ctx.chart.data.labels[ctx.dataIndex];
              return label;
            },
          },
        }
        
      };
    public pieChartLabels: Label[] = ['BIKE','BUS','CAR','LORRY','HUMAN','OTHERS']//['BUS','CAR', 'BIKE'];
    public pieChartData: SingleDataSet = [];//[70, 500, 400];
    public pieChartType: ChartType = 'pie';
    public pieChartLegend = true;
    public pieChartPlugins = [pluginDataLabels];
    public pieChartColors = [
      {
        backgroundColor: ['rgba(255,0,0,0.3)', 'rgba(0,255,0,0.3)', 'rgba(0,0,255,0.3)','rgba(20,80,255,0.9)','rgba(60,10,185,0.3)','rgba(33,22,188,0.8)',],
      },
    ];
} 